.checkbox {
    display: flex;
    justify-content: flex-start;
    align-items: center;

    width: max-content;

    &__control {
        background: white;
        width: 20px;
        height: 20px;

        border: 1px solid $dark25;
        box-sizing: border-box;
        border-radius: 4px;

        display: flex;
        justify-content: center;
        align-items: center;
    }

    &__mark {
        width: 8px;
        height: 8px;
        background-image: url("/assets/images/checkbox/Vector.png");
        background-size: 100%;
    }

    &__label {
        font-family: Montserrat;
        font-style: normal;
        font-weight: normal;
        font-size: 14px;
        line-height: 18px;
        
        color: $dark50;
    }
}